.global-error {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba($dark-grey-color, 0.3);

  &__icon {
    position: absolute;
    width: 50%;
    right: 0;
    bottom: 0;
  }
  &__message {
    position: absolute;
    left: 100px;
    bottom: 120px;
  }
  &__cloud {
  }
}

@media (max-width: 1400px) {
  .global-error {
    &__icon {
      width: 60%;
      bottom: 0;
    }
  }
}

@media (max-width: 1024px) {
  .global-error {
    &__icon {
      width: 80%;
      bottom: 0;
    }
  }
}

@media (max-width: 834px) {
  .global-error {
    &__icon {
      width: 100%;
      bottom: 0;
    }
  }
}
